@import url("https://fonts.googleapis.com/css2?family=Open+Sans&family=Raleway:wght@400;700&display=swap");
html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  min-height: 100vh;
}

body,
h1,
h2,
h3,
h4,
blockquote {
  margin: 0;
}

a {
  outline: none;
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

input,
button,
textarea {
  border: none;
  font: inherit;
  outline: none;
}

button {
  color: inherit;
  cursor: pointer;
}

body {
  font-family: "Open Sans", sans-serif;
  color: #070439;
  line-height: 1.5;
}

:focus-visible {
  outline: 0.125rem dotted #437d85;
  outline-offset: 0.25rem;
}

h1,
h2,
h3 {
  font-family: "Raleway", sans-serif;
}

.sr-only {
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important;
}

.container {
  max-width: 90rem;
  width: 89%;
  margin: 0 auto;
}

.flex {
  display: flex;
}

/*--------------
Forms of both hero and getAccess section
----------------*/
.form {
  width: 100%;
  font-size: 0.875rem;
}
@media (min-width: 71.875em) {
  .form {
    display: grid;
    grid-template-columns: minmax(50%, 20rem) minmax(25%, 12.65rem);
    gap: 1rem;
  }
}

.email__input {
  height: 3.5rem;
  width: 100%;
  padding-left: 1.1875rem;
  border: 1px solid #070439;
  border-radius: 5px;
}
@media (min-width: 71.875em) {
  .email__input {
    height: 2.8125rem;
  }
  .email__input--secondary {
    height: 3.0625rem;
    border-color: transparent;
    box-shadow: 3px 3px 9px rgba(0, 0, 0, 0.2);
  }
}
.email__validation {
  visibility: hidden;
  text-align: left;
  font-size: 0.75rem;
  color: red;
}
.email__validation--white {
  color: white;
}
.email__input:focus:invalid ~ .email__validation {
  visibility: visible;
}
.email__input:focus:invalid {
  border-color: red;
}

.form__btn {
  height: 3.5rem;
  background-color: #3065f8;
  color: white;
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  border-radius: 5px;
  width: 100%;
  box-shadow: 3px 3px 9px rgba(0, 0, 0, 0.2);
}
.form__btn:hover, .form__btn:focus {
  opacity: 0.75;
}
@media (min-width: 71.875em) {
  .form__btn {
    height: 2.8125rem;
  }
  .form__btn--secondary {
    height: 3.0625rem;
    width: 42%;
  }
}

@media (min-width: 45.375em) {
  .getAccess__form {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 1rem;
  }
}
@media (min-width: 45.375em) and (min-width: 71.875em) {
  .getAccess__form {
    margin-top: 1rem;
    margin-right: 3rem;
    display: flex;
    flex-direction: column;
    max-width: 30.625rem;
  }
}

/*-------------
Header section
-------------_*/
.header {
  align-items: center;
  justify-content: space-between;
  padding: clamp(2rem, 6vw, 4rem) 0;
  font-family: "Raleway", sans-serif;
}
.header__logo {
  height: 1.875rem;
}
@media (min-width: 45.375em) {
  .header__logo {
    height: 2.5rem;
  }
}
@media (min-width: 71.875em) {
  .header__logo {
    height: 3.125rem;
  }
}

.nav__item + .nav__item {
  margin-left: 2.0625rem;
}
@media (min-width: 71.875em) {
  .nav__item + .nav__item {
    margin-left: 3.625rem;
  }
}
.nav__link {
  font-weight: var(--fw-regular);
  font-size: 0.9375rem;
  line-height: 1;
  color: #070439;
}

/*------------
Hero Section
-------------*/
.hero-section {
  background-image: url("./../../../images/bg-curve-mobile.svg");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: contain;
  padding-bottom: 9.375rem;
}
@media (min-width: 71.875em) {
  .hero-section {
    /*
    background-image: url("/images/bg-curve-desktop.svg");
    */
    background-image: url("./../../../images/bg-curve-desktop.svg");
    padding-bottom: 16rem;
  }
}

.hero {
  padding: 1.375rem 0;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
@media (min-width: 71.875em) {
  .hero {
    flex-direction: row-reverse;
    justify-content: space-between;
    text-align: left;
  }
}
.hero__img {
  max-width: 40rem;
}
.hero__content {
  max-width: 52.8125rem;
}
@media (min-width: 71.875em) {
  .hero__content {
    width: 46%;
    margin-right: 1.25rem;
  }
}
.hero__title {
  font-size: 1.875rem;
  margin-top: 3.125rem;
}
@media (min-width: 45.375em) {
  .hero__title {
    font-size: 2.1875rem;
    margin-top: 3.75rem;
  }
}
@media (min-width: 71.875em) {
  .hero__title {
    font-size: 2.5rem;
    margin-top: 0;
  }
}
@media (min-width: 71.875em) {
  .hero__disc {
    font-size: 1.125rem;
  }
}

.productive-section {
  background-color: #f6f6fe;
}

.productive {
  flex-direction: column-reverse;
  align-items: center;
  padding: 3.75rem 0;
}
@media (min-width: 71.875em) {
  .productive {
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 71.875em) {
  .productive__content {
    width: 54%;
    margin-right: 1.25rem;
  }
}
@media (min-width: 45.375em) {
  .productive__img {
    max-width: 550px;
  }
}
@media (min-width: 71.875em) {
  .productive__img {
    align-self: flex-start;
    padding-top: 0.625rem;
  }
}
.productive__title {
  margin-top: 5.625rem;
  text-align: center;
}
@media (min-width: 45.375em) {
  .productive__title {
    font-size: 2.1875rem;
    margin-bottom: 2.1875rem;
  }
}
@media (min-width: 71.875em) {
  .productive__title {
    font-size: 2.375rem;
    text-align: left;
    margin-top: 0;
  }
}
@media (min-width: 71.875em) {
  .productive__disc {
    font-size: 1.0625rem;
    max-width: 35.625rem;
  }
}
.productive__link {
  align-items: center;
  font-size: 0.9375rem;
  color: #3c9f8f;
  border-bottom: 2px solid #3c9f8f;
  padding-bottom: 0.3125rem;
  width: fit-content;
  margin: 0 auto;
}
.productive__link:hover {
  opacity: 0.75;
}
@media (min-width: 71.875em) {
  .productive__link {
    margin: 0;
    font-size: 1.0625rem;
  }
}
.productive__link svg {
  margin-left: 0.3125rem;
}
.productive__link svg:hover {
  opacity: 0.75;
}

.testimonial {
  max-width: 26.25rem;
  margin: 2.625rem auto;
  padding: 1.5625rem 2rem;
  border-radius: 0.3125rem;
  line-height: 1.6;
  background-color: white;
  box-shadow: -2px 2px 13px rgba(0, 0, 0, 0.2);
}
@media (min-width: 71.875em) {
  .testimonial {
    max-width: 22.375rem;
    margin-left: 0;
    margin-right: 0;
  }
}

blockquote {
  padding: 1rem 0;
}
@media (min-width: 71.875em) {
  blockquote {
    font-size: 0.875rem;
  }
}

.profile {
  align-items: center;
}
.profile__title {
  font-size: 0.8125rem;
}
.profile__disc {
  font-size: 0.625rem;
  margin: 0;
}
.profile__img {
  height: 2.5rem;
  border-radius: 50%;
  margin-right: 0.625rem;
}

.getAccess-section {
  background-color: #585989;
}

.getAccess {
  padding: 5.125rem 0;
  text-align: center;
  color: white;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 45.375em) {
  .getAccess {
    width: 78%;
  }
}
@media (min-width: 71.875em) {
  .getAccess {
    width: 90%;
    flex-direction: row;
    justify-content: space-between;
    text-align: left;
    padding: 5.8125rem 0 3.3125rem 0;
  }
}
@media (min-width: 71.875em) {
  .getAccess__content {
    width: 50%;
    max-width: 33.75rem;
  }
}
.getAccess__title {
  font-size: 1.625rem;
}
@media (min-width: 71.875em) {
  .getAccess__title {
    font-size: 2.125rem;
  }
}
.getAccess__disc {
  margin: 1.5625rem 0;
}
@media (min-width: 71.875em) {
  .getAccess__disc {
    font-size: 1.0625rem;
  }
}

.footer-section {
  background-color: #070439;
  padding-top: 5.8125rem;
  padding-bottom: 2.75rem;
}
@media (min-width: 71.875em) {
  .footer-section {
    padding-top: 6.6875rem;
    padding-bottom: 1.5625rem;
  }
}

.footer a {
  color: white;
  font-size: 1.0625rem;
}
.footer__logo svg {
  height: 3.25rem;
}
.footer__logo svg * {
  fill: white;
  height: 3.25rem;
}
.footer__content {
  margin-top: 3.5rem;
}
@media (min-width: 45.375em) {
  .footer__content {
    margin-top: 1.375rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
.footer__nav:not(:first-child) {
  margin-bottom: 4.75rem;
}
@media (min-width: 45.375em) {
  .footer__nav:not(:first-child) {
    margin-bottom: 0;
  }
}
@media (min-width: 45.375em) {
  .footer__nav {
    display: flex;
    flex-direction: row;
    width: 30%;
    justify-content: space-between;
  }
}
.footer__item {
  margin-top: 4.75rem;
}
.footer__item:not(:first-child) {
  margin-top: 1.125rem;
}
@media (min-width: 45.375em) {
  .footer__item {
    margin-top: 0;
  }
}
.footer__link:hover, .footer__link:focus {
  color: #8c8eff;
}

.info__item {
  display: flex;
  align-items: center;
}
.info__item:not(:first-child) {
  margin-top: 1.25rem;
}
.info__item a {
  margin-left: 1rem;
}

.social-icons__list {
  justify-content: center;
}
@media (min-width: 71.875em) {
  .social-icons__list {
    margin-right: 10.625rem;
  }
}
.social-icons__items:not(:first-child) {
  margin-left: 0.75rem;
}
.social-icons__link {
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid inherit;
}
.social-icons__link::before {
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  line-height: 1;
}
.social-icons__link:hover, .social-icons__link:focus {
  color: #8c8eff;
  border-color: #8c8eff;
}
.social-icons--facebook::before {
  font-family: "Font Awesome 5 Brands";
  font-weight: 400;
  content: "\f39e";
}
.social-icons--twitter::before {
  font-family: "Font Awesome 5 Brands";
  content: "\f099";
  font-weight: 400;
}
.social-icons--instagram::before {
  font-family: "Font Awesome 5 Brands";
  content: "\f16d";
  font-weight: 400;
}

.attribution {
  font-size: 11px;
  text-align: center;
  color: white;
  margin-top: 2.1875rem;
  margin-bottom: 0;
}

.attribution a {
  font-size: 12px;
  color: #3065f8;
}